<template>
  <div class="container py-5">
    <!-- AdminNav Component -->
    <AdminNav />

    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">
            #
          </th>
          <th scope="col">
            Email
          </th>
          <th scope="col">
            Role
          </th>
          <th
            scope="col"
            width="140"
          >
            Action
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="user in users"
          :key="user.id"
        >
          <th scope="row">
            {{user.id}}
          </th>
          <td>{{user.email}}</td>
          <td v-if="user.isAdmin">admin</td>
          <td v-else>user</td>
          <td>
            <button
              type="button"
              class="btn btn-link"
              v-if="user.isAdmin"
              v-show="user.id !== currentUser.id"
              @click.stop.prevent="toggleRole(user.id, false)"
            >
              set as user
            </button>
            <button
              type="button"
              class="btn btn-link"
              v-else
              @click.stop.prevent="toggleRole(user.id, true)"
            >
              set as admin
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import AdminNav from '../components/AdminNav.vue'
import { mapState } from 'vuex'
import adminAPI from '../apis/admin'
import { Toast } from '../utils/helpers'

export default {
  data() {
    return {
      users: [],
    }
  },
  created() {
    this.fetchUsers()
  },
  methods: {
    async fetchUsers() {
        try {
            const { data } = await adminAPI.users.get()
            this.users = data.users
        } catch(err) {
            Toast.fire({
                icon: 'error',
                title: '無法取得使用者資料，請稍後再試'
            })
            console.log('error', err)
        }
    },
    async toggleRole(userId, isAdmin) {
      try {
        const payLoad = JSON.stringify({ isAdmin: isAdmin.toString() })
        const { data } = await adminAPI.users.toggleRole({ userId, payLoad })
        console.log(data)
        if(data.status !== 'success') {
          throw new Error(data.message)
        }
        this.users = this.users.map(user => {
          if(user.id === userId) {
            return {
              ...user,
              isAdmin: !user.isAdmin
            }
          }
          return user
        })
        Toast.fire({
          icon: 'success',
          title: '使用者權限修改成功'
        })
      } catch(err) {
        Toast.fire({
          icon: 'error',
          title: '無法修改使用者權限，請稍後再試'
        })
        console.log('error', err)
      }
    }
  },
  components: {
    AdminNav,
  },
  computed: {
      ...mapState(['currentUser'])
  }
}
</script>